"use client";

import { useEffect, useState } from "react";
import Link from "next/link";

interface ParsedRow {
  [key: string]: string | number;
}

interface ParsedTimetable {
  rows: ParsedRow[];
  meta?: Record<string, unknown>;
}

interface TimetableVersion {
  id: number;
  tqi: number;
  timetable: ParsedRow[];
}

export default function GeneratorPage() {
  const [parsed, setParsed] = useState<ParsedTimetable | null>(null);
  const [versions, setVersions] = useState<TimetableVersion[]>([]);
  const [loading, setLoading] = useState(false);

  // Load parsed timetable from localStorage
  useEffect(() => {
    try {
      const saved = localStorage.getItem("parsedTimetable");
      if (!saved) return;

      const obj = JSON.parse(saved);
      setParsed(obj);
    } catch (err) {
      console.error("Failed to load parsed timetable:", err);
    }
  }, []);

  // If no timetable uploaded
  if (!parsed || !parsed.rows || parsed.rows.length === 0) {
    return (
      <div className="min-h-screen bg-white text-gray-900 text-center p-10">
        <h1 className="text-3xl font-bold mb-4">Generate Timetable</h1>
        <p className="text-gray-500 mb-4">No file uploaded yet.</p>

        <Link
          href="/generate-timetable/upload"
          className="bg-indigo-600 text-white px-6 py-3 rounded-lg hover:bg-indigo-700"
        >
          Upload File →
        </Link>
      </div>
    );
  }

  // Generate timetable versions
  const generate = async () => {
    setLoading(true);

    const res = await fetch("/api/generate", {
      method: "POST",
      body: JSON.stringify({ parsed }),
    });

    const data = await res.json();
    setVersions(data.versions);

    setLoading(false);
  };

  return (
    <div className="min-h-screen bg-white text-gray-900 p-10">

      {/* Page Title */}
      <h1 className="text-4xl font-bold mb-6">Uploaded Timetable</h1>

      {/* Timetable Preview Table */}
      <div className="overflow-x-auto mb-10 border border-gray-300 rounded-lg">
        <table className="min-w-full">
          <thead className="bg-gray-100">
            <tr>
              {Object.keys(parsed.rows[0]).map((col) => (
                <th
                  key={col}
                  className="px-4 py-3 border-b text-left font-semibold text-gray-700"
                >
                  {col.toUpperCase()}
                </th>
              ))}
            </tr>
          </thead>

          <tbody>
            {parsed.rows.map((row, idx) => (
              <tr
                key={idx}
                className={`${
                  idx % 2 === 0 ? "bg-white" : "bg-gray-50"
                } hover:bg-indigo-50 transition`}
              >
                {Object.values(row).map((value, colIdx) => (
                  <td
                    key={colIdx}
                    className="px-4 py-2 border-b text-gray-700"
                  >
                    {String(value)}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Generate Timetable Button */}
      <button
        onClick={generate}
        disabled={loading}
        className="bg-indigo-600 text-white px-8 py-3 rounded-lg shadow hover:bg-indigo-700 transition disabled:opacity-50"
      >
        {loading ? "Generating…" : "Generate Timetables"}
      </button>

      {/* Generated Versions */}
      {versions.length > 0 && (
        <div className="mt-12">
          <h2 className="text-3xl font-bold mb-6">Generated Versions</h2>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {versions.map((v) => (
              <div
                key={v.id}
                className="border p-6 rounded-xl shadow bg-gray-50"
              >
                <h3 className="text-xl font-semibold mb-2">
                  Version {v.id}
                </h3>

                <p className="text-gray-700 mb-3">
                  TQI Score:{" "}
                  <span className="font-bold text-indigo-600">
                    {v.tqi.toFixed(2)}
                  </span>
                </p>

                <button className="bg-white border border-indigo-600 text-indigo-600 px-4 py-2 rounded hover:bg-indigo-100 w-full">
                  View Details →
                </button>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}
